<template>
	
	<div class="classify_list">
		<div class="list_header">
			<div class="classify_list_title">
				<i class="list_left fa fa-chevron-left" @click="goClassify"></i>
				<p class="list_title">{{lisData[$store.state.type || 'gyyx']['title']}}</p>
				<i class="list_search fa fa-search"></i>
			</div>
			<div class="swiper-container menu-swiper swiper-container-horizontal">		
		        <ul class="swiper-wrapper list_tab">
		        	<li class="swiper-slide all" style="width: 1.093333rem;">
		        		<span></span>
		        	</li>
		            <li class="swiper-slide" style="width: 1.093333rem;"  v-for="(item,idx) in (lisData[$store.state.type || 'gyyx']['data'])">
		                <span>{{item}}</span>
		            </li>
		        </ul>		
			</div>
		</div>	
		

		<div class="listBox">
			<listDatagrid></listDatagrid>
		</div>

	</div>
</template>

<script type="text/javascript">
	import '../../css/classify_list.css';
	import '../../assets/css/swiper.min.css'
	import Swiper from '../../assets/js/swiper.min.js'

	import listDatagrid from '../list/listDatagrid.vue'
	export default{

		data(){
			return{
				lisData:{
					gyyx:{title:'',data:['','','','','/','']},
					xxsg:{title:'',data:['','','','','/','','','']},
					xxsp:{title:'',data:['','']},
					lygh:{title:'',data:['']},
					ctbj:{title:'',data:['','','']},
					jsyl:{title:'',data:[]},
					lhqk:{title:'',data:['','']}
					
				}
			}

		},
		mounted(){  
	         var swiper = new Swiper('.swiper-container', {
	        	lopper:true,
		      	slidesPerView: 5,
		      	slidesOffsetBefore : 100,
		      	pagination: {
			        el: '.swiper-pagination',
			        clickable: true
			      },
		    });
		},
		components:{
			listDatagrid
		},
		methods:{
			goClassify:function(){
				this.$router.push({name:'classify'});
			}
		}

	}
</script>


