<template>
	
	<div class="classify">
		<div class="c_search">
			<span><i class="fa fa-search"></i></span>
		</div>
		<main class="classify_main">
			<div class="classify_left">
				<ul>

					<li v-for="obj in menus" :key="obj.path" @click="clearActive">
						<router-link :to="obj.path">{{obj.text}}</router-link>
					</li>					


					</ul>
				</div>
				<div class="classify_right">
					<router-view>
						
					</router-view>
				</div>
			</main>
		</div>	
	</div>
</template>
<script>
	import '../../../css/classify.css';
	import $ from 'jquery'
	export default{
		data(){
            return {
                menus : [
                    {path: '/classify/gyyx', text: ''},
					{path:'/classify/xxsg', text:''},
					{path: '/classify/xxsp', text: ''},
					{path:'/classify/lygh', text:''},
					{path: '/classify/ctbj', text: ''},
					{path:'/classify/jsyp', text:''}	
                ]
            }
        },
		mounted(){
			$('.classify_main .classify_left a').first().addClass('classifyActive')
			
			     
			     
		},
		methods:{
			clearActive(){
				$('.classify_main .classify_left a').removeClass('classifyActive');
				console.log(666)
				     
			}
		}
	}
</script>


